.Projects{
    max-width: 1020px;
    margin: auto;
    justify-content: space-between;
}

 .Projects img{
    padding: 10px;
    will-change: transform;
    transform: perspective(1500px) rotateX(0deg) rotateY(0deg);
 }

 .Projects img:hover{
    transform: perspective(1500px) rotateY(10deg);
    transition: transform .5s;
 }